<template>
  <div class="box">
    <!-- 顶部按钮 -->
    <div class="btnall">
        <van-button type="default" class="btn" size="mini" @click="btn(1)"><img class="topimg" src="@/assets/third/hospital.png"/> <span>周边医院</span></van-button>
        <van-button type="default" class="btn" size="mini" @click="btn(2)"><img class="topimg" src="@/assets/third/shop.png"/><span>周边超市</span></van-button>
        <van-button type="default" class="btn" size="mini" @click="btn(3)"><img class="topimg" src="@/assets/third/play.png"/><span>周边娱乐</span></van-button>
        <van-button v-if="userInfo.friends.length > 0" type="default" class="btn" size="mini" @click="showBox"><img class="topimg" src="@/assets/third/search.png"/><span>查看好友</span></van-button>
    </div>
    <!-- 地图容器 -->
    <div id="container"></div>
    <!-- 定位图标 -->
    <div class="refresh" @click="refresh"><img src="../../assets/third/dingwei.png" alt="" class="img"></div>
    <!-- 好友列表 -->
    <van-popup v-model="show">
      <van-radio-group v-model = "whoFir">
        <van-cell-group v-if="userInfo.friends.length > 0" class="cellGroup">
          <van-cell v-for="(item, index) in userInfo.friends" :key="index" clickable @click="whoFir = index">
            <template slot="title">
              <img :src="imgbox[item.headimg].src" class="imgbg" />
              <div class="imgcot">
                <div>{{item.name}}</div>
                <div>({{item.tel}})</div>
              </div>
            </template>
            <van-radio slot="right-icon" :name="index" :value="index" />
          </van-cell>
        </van-cell-group>
      </van-radio-group>
      <van-button type="primary" class="btn" size="block" @click="sure">确定</van-button>
    </van-popup>
  </div>
</template>

<script>
import AMap from "AMap";
import { mapState, mapMutations } from 'vuex';
import { Toast } from 'vant'
export default {
  name: "service",
  data() {
    return {
      map: null,
      show:false,
      marker: null,
      markers: [],
      whoFir: 0,
      otherlng: null, // 查看好友时好友坐标
      otherlat: null, // 查看好友时好友坐标
      imgbox: [
        { id: 0, src: require("../../assets/fourth/manf.png") },
        { id: 1, src: require("../../assets/fourth/womanf.png") },
        { id: 2, src: require("../../assets/fourth/mans.png") },
        { id: 3, src: require("../../assets/fourth/womans.png") }
      ]
    };
  },
  computed: {
    ...mapState(['userInfo'])
  },
  watch: {
    markers(value) {
      let that = this
      if(value.length !=0) {
        that.getMarkers()
      }
    }
  },
  methods: {
    ...mapMutations(['updateUserInfo']),
    getMarkers() {
      let that = this
      that.map.destroy()
      that.mapCreate()
      var infoWindow = new AMap.InfoWindow({
        offset: new AMap.Pixel(-15, -30)
        });
      for (var i = 0; i < that.markers.length; i++) {
        var marker = new AMap.Marker({
            position: [that.markers[i].lng,that.markers[i].lat],
            icon: new AMap.Icon({            
              image: require("../../assets/third/markers.png"),
              size: new AMap.Size(30, 30),  //图标大小
              imageSize:new AMap.Size(30,30)
            }),
            offset: new AMap.Pixel(-15, -30),
            map: that.map
        });
        marker.content =`<div class="mapbox">
          <div style="
          font-size: 12px;
          text-overflow:ellipsis;
          white-space:nowrap;
          margin-top: 10px
          "> ${that.markers[i].name} </div>
          <div style="
          font-size: 12px;
          text-overflow:ellipsis;
          white-space:nowrap;
          ">地址: ${that.markers[i].address}</div> 
          <div style="
          font-size: 12px;
          text-overflow:ellipsis;
          white-space:nowrap;
          ">电话: ${that.markers[i].tel}</div> 
          <div style="
          font-size: 12px;
          text-overflow:ellipsis;
          white-space:nowrap;
          ">距离: ${that.markers[i].distance}m</div> 
        </div>`;
        marker.on('click', function(e) {
          infoWindow.setContent(e.target.content);
          infoWindow.open(that.map, e.target.getPosition())
        });
        marker.emit('click', {target: marker});
      }
      that.map.setFitView();
    },
    mapCreate() {
      let that = this
      var lng = this.userInfo.lng
      var lat = this.userInfo.lat
      that.map = new AMap.Map("container", {
        resizeEnable: true, //是否监控地图容器尺寸变化
        zoom: 14, //初始化地图层级
        center: [lng, lat], //初始化地图中心点
        showBuildingBlock:true,
      });
        that.marker = new AMap.Marker({
            position: [lng, lat],
            icon: new AMap.Icon({
              image: require("../../assets/third/zuobiao.png"),
              size: new AMap.Size(30, 30),  //图标大小
              imageSize:new AMap.Size(30,30)
            }),
            offset: new AMap.Pixel(-15, -30)
        });
        that.marker.setMap(that.map);
      AMap.plugin(["AMap.ToolBar"], function() {
        // 在图面添加工具条控件，工具条控件集成了缩放、平移、定位等功能按钮在内的组合控件
        that.map.addControl(
          new AMap.ToolBar({
            // 简易缩放模式，默认为 false
            liteStyle: true
          })
        );
      });
      that.map.addControl(new AMap.Scale());

    },
    pos() {
      let that = this
    // 原生定位
      // if(!window.plus) {
      //   Toast('定位失败，请打开定位')
      // } else {
      //   window.plus.geolocation.getCurrentPosition(function(p) {
      //     var lnglats = p.coords; // Array.<LngLat>
      //     that.userInfo.lng = lnglats.lng
      //     that.userInfo.lat = lnglats.lat
      //     Toast('定位成功' + lnglats.lng + ',' + lnglats.lat)
      //     let model = {
      //       lng: lnglats.lng,
      //       lat: lnglats.lat,
      //       tel: that.userInfo.tel
      //     }
      //     that.$socket.emit('posUpdate', model)
      //     that.mapCreate()
      //     return that.updateUserInfo(that.userInfo)
      //   }, function(e) {
      //     Toast('定位失败'+ e.message)
      //   }, {
      //     enableHighAccuracy: 'true',
      //     timeout: 10000,
      //     provider: 'amap'
      //   })
      // }
    // 高德定位
      AMap.plugin('AMap.Geolocation', function() {
      var geolocation = new AMap.Geolocation({
        // 是否使用高精度定位，默认：true
        enableHighAccuracy: true,
        // 自动偏移坐标，偏移后的坐标为高德坐标，默认：true
        convert: false,
        // 设置定位超时时间，默认：无穷大
        timeout: 10000,
        // 定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
        zoomToAccuracy: true,
        // 显示定位按钮，默认：true
        showButton: false
      })
      geolocation.getCurrentPosition()
      AMap.event.addListener(geolocation, 'complete', function(data) {
        var lnglat = data.position
        that.userInfo.lng = lnglat.R
            that.userInfo.lat = lnglat.Q
            Toast('定位成功' + lnglat.R + ',' + lnglat.Q)
            let model = {
              lng: lnglat.R,
              lat: lnglat.Q,
              tel: that.userInfo.tel
            }
            that.$socket.emit('posUpdate', model)
            that.updateUserInfo(that.userInfo)
            return that.mapCreate()
        // AMap.convertFrom([data.position.R,data.position.Q], 'gps', function (status, result) {
        //   if (result.info === 'ok') {
        //     var lnglats = result.locations[0]; // Array.<LngLat>
        //     that.userInfo.lng = lnglats.R
        //     that.userInfo.lat = lnglats.Q
        //     Toast('定位成功' + lnglats.R + ',' + lnglats.Q)
        //     let model = {
        //       lng: lnglats.R,
        //       lat: lnglats.Q,
        //       tel: that.userInfo.tel
        //     }
        //     that.$socket.emit('posUpdate', model)
        //     that.mapCreate()
        //     return that.updateUserInfo(that.userInfo)
        //   }
        // });
      })
      AMap.event.addListener(geolocation, 'error', function(data) {
        Toast('失败原因排查信息:'+data.message);
      })
    })
    },
    otherPos() {
      let that = this
      var lng = this.otherlng
      var lat = this.otherlat
      that.map = new AMap.Map("container", {
        resizeEnable: true, //是否监控地图容器尺寸变化
        zoom: 14, //初始化地图层级
        center: [lng, lat], //初始化地图中心点
        showBuildingBlock:true,
      });
        that.marker = new AMap.Marker({
            position: [lng, lat],
            icon: new AMap.Icon({            
              image: require("../../assets/third/pos.png"),
              size: new AMap.Size(30, 30),  //图标大小
              imageSize:new AMap.Size(30,30)
            }),
            offset: new AMap.Pixel(-15, -30)
        });
        that.marker.setMap(that.map);
      AMap.plugin(["AMap.ToolBar"], function() {
        // 在图面添加工具条控件，工具条控件集成了缩放、平移、定位等功能按钮在内的组合控件
        that.map.addControl(
          new AMap.ToolBar({
            // 简易缩放模式，默认为 false
            liteStyle: true
          })
        );
      });
      that.map.addControl(new AMap.Scale());
    },
    btn(type) {
      let that = this
      that.markers = []
      var typename = ""
      if(type == '1'){
        typename='医院'
      }else if(type == '2'){
        typename ='超市'
      }else if(type == '3'){
        typename ='娱乐'
      }
      var lng = that.userInfo.lng
      var lat = that.userInfo.lat
      AMap.service(["AMap.PlaceSearch"], function() {
        //构造地点查询类
        var placeSearch = new AMap.PlaceSearch({ 
          type: typename, // 兴趣点类别
        });
        placeSearch.searchNearBy('',[lng, lat] , 3000, function(status, result) {
          that.markers = []
          if(result && result.poiList && result.poiList.pois.length > 0) {
            var items = result.poiList.pois;
            for(var i=0;i<items.length;i++){
              that.markers.push({
                lng: items[i].location.R,
                lat: items[i].location.Q,
                name: items[i].name,
                distance: items[i].distance,
                tel: items[i].tel || '无',
                address: items[i].address || '无'
              })
            }
          } else {
            Toast('周围没有' + typename)
          }
        });
      })
    },
    showBox() {
      this.show = true
      this.whoFir = 0
    },
    sure() {
      var data = this.userInfo.friends[this.whoFir]
      this.show = false
      this.map.destroy()
      this.$socket.emit('otherPos', data)
    },
    refresh() {
      this.map.destroy()
      this.mapCreate()
      this.pos()
    }
  },
  mounted() {
    this.pos();
    this.mapCreate();
    this.$socket.removeAllListeners('posUpdate');
    this.$socket.removeAllListeners('otherPos');
    // 更新坐标
    this.$socket.on('posUpdate', res => {
        if(res.tel == this.userInfo.tel) {
          return this.updateUserInfo(res)
        }
    })
    this.$socket.on('otherPos', res => {
        if(res.tel == this.userInfo.friends[this.whoFir].tel) {
          this.otherlng = res.lng
          this.otherlat = res.lat
          this.otherPos()
        }
    })
  }
};
</script>
<style lang="scss" scoped>
.box {
  width: 100%;
  height: calc(100vh - 96px);
  #container {
    width: 100%;
    height: calc(100vh - 120px);
    // margin-top: 20px;
  }
  .btnall {
      height: 24px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-around;
      .topimg {
        width:20px;
        height:20px;
        float: left;
      }
      span{
        font-size: 12px;
      }
  }
}
.refresh {
  position: fixed;
  bottom: 173px;
  right:16px;
}
// 地图信息窗口

.amap-info-content >>> .amap-info-outer {
  padding: 0;
  background: pink !important;
    .mapbox {
    width: 80px;
    height:40px;
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    background: white;
    .name{
      width: 100%;
      background-color: lightgray;
      font-size: 14px;
      text-overflow:ellipsis;
      white-space:nowrap;
    }
  }
}
.cellGroup {
  width: calc(100vw - 80px);
  height: calc(100vh - 200px);
  overflow: scroll;
}
.imgbg {
  width:40px;
  height:40px;
  float: left;
}
.imgcot {
  float: left;
  margin-top: -4px;
  margin-left: 10px;
}

</style>